/* Colors */
:root {
  --bgColor: #131722;
  --lightBgColor: #24baef;
  --darkBgColor: #09516b;
  --bgTransparent: rgba(36, 186, 239, 0.6);
  --fontColor: #fff;
  --borderColor: #a8a8a83d;
  --borderHeight: 1px;
  --secondFontColor: #949cb0;
}

/* Global */

html {
  font-size: 100%;
  scroll-behavior: smooth;
  font-family: "Montserrat Alternates", sans-serif;
}

body {
  background-color: #131722;
  background-color: var(--bgColor);
  color: #fff;
  color: var(--fontColor);
}

.container {
  width: 95% !important;
  max-width: 95% !important;
}

/* login */
.login {
  height: calc(100vh - 128.8px)
}

/* register */
.register {
  height: calc(100vh - 128.8px)
}

/* footer */


footer {
  box-shadow: 0 0 10px rgba(0,0,0,.35);
}

footer .icons_wrapper i {
  font-size: 20px;
  color: #949cb0;
  color: var(--secondFontColor);
  margin: 0 10px;
  cursor: pointer;
}

footer p {
  font-size: 14px;
  color: #949cb0;
  color: var(--secondFontColor);
  margin-top: 20px;
}

/* Errors */
.alert {
  display: inline-block;
  width: auto;
  padding: 5px;
  margin: 5px 0;
  font-size: 12px;
}
